import styled from "styled-components";
import {
  motion,
  Variants,
  useAnimation,
  useScroll,
  useMotionValueEvent,
  useAnimate,
} from "framer-motion";
import { NavLink, useNavigate } from "react-router-dom";
import { useState, useRef } from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import { ISearchFormData } from "../types";

const Nav = styled(motion.nav)`
  z-index: 99;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  font-size: 16px;
  padding: 20px 60px;
  box-sizing: border-box;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.8),
    rgba(0, 0, 0, 0)
  );
`;

const Column = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled(motion.svg)`
  margin-right: 50px;
  width: 95px;
  height: 25px;
  fill: ${(props) => props.theme.red};
`;

const Items = styled.ul`
  display: flex;
  align-items: center;
`;

const Item = styled.li`
  margin-right: 20px;
  color: ${(props) => props.theme.white.darker};
  transition: color 0.3s ease-in-out;
  &:hover {
    color: ${(props) => props.theme.white.lighter};
  }
`;

const Search = styled.form`
  color: white;
  svg {
    height: 25px;
    position: absolute;
    right: 10px;
  }
  display: flex;
  align-items: center;
  position: relative;
  right: 30px;
`;

const Circle = styled(motion.span)`
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: ${(props) => props.theme.red};
  position: absolute;
  top: 20px;
`;

const StyledNavLink = styled(NavLink)`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Input = styled(motion.input)`
  background-color: black;
  border: 1px solid white;
  height: 35px;
  transform-origin: right;
  width: 320px;
  position: absolute;
  right: 10px;
  padding: 5px 0 5px 40px;
  border-radius: 5px;
  color: ${(props) => props.theme.white.lighter};
  font-size: 20px;
  box-sizing: border-box;
`;

const Magnifier = styled(motion.svg)`
  cursor: pointer;
`;

const logoVariants: Variants = {
  normal: {
    fillOpacity: 1,
  },

  active: {
    fillOpacity: [0, 1, 0],
    transition: {
      repeat: Infinity,
      duration: 1,
    },
  },
};

export default function Header() {
  const [isSearchBarOpen, setIsSearchBarOpen] = useState(false);
  const [scope, animate] = useAnimate();
  const { scrollY } = useScroll();
  const formRef = useRef<HTMLFormElement>(null);
  const { register, handleSubmit, setValue } = useForm<ISearchFormData>();
  const navigate = useNavigate();

  const toggleIsOpen = () => {
    if (!isSearchBarOpen) {
      formRef.current?.keyword.focus();
      animate([
        [".searchInput", { scaleX: 1 }, { duration: 0.4 }],
        [".magnifier", { x: -290 }, { at: "<", duration: 0.4 }],
      ]);

      setIsSearchBarOpen(true);
    } else {
      animate([
        [".searchInput", { scaleX: 0 }, { duration: 0.4 }],
        [".magnifier", { x: 0 }, { at: "<", duration: 0.4 }],
      ]);
      setIsSearchBarOpen(false);
    }
  };

  const handleInputBlur = () => {
    animate([
      [".searchInput", { scaleX: 0 }, { duration: 0.4 }],
      [".magnifier", { x: 0 }, { at: "<", duration: 0.4 }],
    ]);
    setIsSearchBarOpen(false);
  };

  const onValid: SubmitHandler<ISearchFormData> = (data) => {
    navigate(`/search?keyword=${data.keyword}`);
    setValue("keyword", "");
  };

  useMotionValueEvent(scrollY, "change", (scrollYValue) => {
    const isScrolledDown = scrollYValue > 20;
    if (isScrolledDown) {
      animate(
        scope.current,
        { backgroundColor: "rgba(0,0,0,1)" },
        { duration: 0.3 }
      );
    } else {
      animate(
        scope.current,
        { backgroundColor: "rgba(0,0,0,0)" },
        { duration: 0.3 }
      );
    }
  });

  return (
    <Nav ref={scope}>
      <Column>
        <Logo
          xmlns="http://www.w3.org/2000/svg"
          width="1024"
          height="276.742"
          viewBox="0 0 1024 276.742"
          variants={logoVariants}
          initial="normal"
          whileHover="active"
        >
          <motion.path d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z" />
        </Logo>
        <Items>
          <Item>
            <StyledNavLink to="/movies">
              {({ isActive }) =>
                isActive ? (
                  <>
                    <span>Home</span>
                    <Circle layoutId="circle" />
                  </>
                ) : (
                  <span>Home</span>
                )
              }
            </StyledNavLink>
          </Item>
          <Item>
            <StyledNavLink to="/tvSeries">
              {({ isActive }) =>
                isActive ? (
                  <>
                    <span>TV</span>
                    <Circle layoutId="circle" />
                  </>
                ) : (
                  <span>TV</span>
                )
              }
            </StyledNavLink>
          </Item>
        </Items>
      </Column>
      <Column>
        <Search
          ref={formRef}
          onBlur={handleInputBlur}
          onSubmit={handleSubmit(onValid)}
        >
          <Input
            initial={{ scaleX: 0 }}
            className="searchInput"
            {...register("keyword", {
              minLength: {
                value: 2,
                message: "Please enter at least 2 letters.",
              },
            })}
          />
          <Magnifier
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
            onClick={toggleIsOpen}
            className="magnifier"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            ></path>
          </Magnifier>
        </Search>
      </Column>
    </Nav>
  );
}
